.custom-card {
  width: 100%;
  
  .ant-card-body {
    padding: 8px;
  }

  .ant-card-cover {
    img {
      object-fit: cover;
      height: 300px;
      width: 100%;
    }
  }

  .card-content {
    .card-title {
      font-size: 16px;
      font-weight: bold;
      margin-bottom: 4px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .card-description {
      font-size: 14px;
      color: #666;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      overflow: hidden;
      line-clamp: 3; // Add this line for compatibility
      line-height: 1.3;
    }
  }
}

.card-popover {
  max-width: 300px;

  .ant-popover-inner-content {
    padding: 12px;
  }

  .card-popover-content {
    h3 {
      font-size: 18px;
      margin-bottom: 8px;
    }

    p {
      margin-bottom: 12px;
      line-height: 1.4;
    }

    .genre-tags {
      h4 {
        margin-bottom: 8px;
      }

      .monochrome-tag {
        margin-bottom: 4px;
        margin-right: 4px;
        background-color: #f0f0f0;
        border: 1px solid #d9d9d9;
        color: #000000;

        &:hover {
          background-color: #e0e0e0;
        }
      }
    }
  }
}
